@media only screen
  and (min-width: 1024px) {

    body {
      font-size: 1.5em;
    }

    .contacts {
      width: 70%;
      margin: 0 auto;
    }

}

@media only screen
  and (max-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2) {

  body {
    font-size: 1em;
  }

}



