.contact-upper,
.contact-lower {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.contact-lower {
  -ms-flex-align: end;
      align-items: flex-end;
  margin-top: 15px;
}

.contact-button {
  font-size: 1em;
}

#dlt-btns {
  margin-top: 50px;
}

#dlt-btns button {
  float: none;
}



